@media (min-width: 960px) {
  .col-md-1 {
    width: 50px; }
  .col-md-2 {
    width: 130px; }
  .col-md-3 {
    width: 210px; }
  .col-md-4 {
    width: 290px; }
  .col-md-5 {
    width: 370px; }
  .col-md-6 {
    width: 450px; }
  .col-md-7 {
    width: 530px; }
  .col-md-8 {
    width: 610px; }
  .col-md-9 {
    width: 690px; }
  .col-md-10 {
    width: 770px; }
  .col-md-11 {
    width: 850px; }
  .col-md-12 {
    width: 930px; } }

.row {
  display: flex;
  flex-wrap: wrap; }
  .row [class^="col-md"] {
    padding: 0px 15px; }

@media (max-width: 960px) {
  [class^="col-md"] {
    flex-basis: 100%; } }

.container {
  width: 960px;
  max-width: 960px;
  margin: 0px auto;
  padding: 0px 15px; }
  @media (max-width: 960px) {
    .container {
      width: 100vw;
      max-width: 100vw;
      padding: 0; } }

.pull-right {
  float: right; }

.pull-left {
  float: left; }

.login-form {
  margin: 0px auto; }
  @media (min-width: 960px) {
    .login-form {
      width: 30%; } }

.albumList {
  border: 1px solid #616161;
  border-radius: 5px; }
  .albumList a {
    text-decoration: none;
    display: block;
    padding: 5px 15px; }
    .albumList a.active {
      font-weight: bold; }
    .albumList a:not(:last-child) {
      border-bottom: 1px solid #616161; }

#albumImages img {
  width: 150px;
  height: 150px;
  cursor: pointer; }

#previewAlbumImage img {
  max-width: 100%;
  margin: 15px auto;
  display: block; }

.form-item {
  display: grid;
  grid-template-columns: 1fr 2fr;
  margin-bottom: 10px; }
  .form-item label {
    font-weight: 400;
    flex-basis: content;
    line-height: 30px; }
    .form-item label:after {
      content: ":";
      display: inline-block;
      margin: 0px 5px; }
  .form-item input {
    height: 30px;
    font-size: 13px; }

button[type="submit"] {
  background-color: #1976d2;
  color: #ffffff;
  border: none;
  padding: 6px 10px;
  border-radius: 5px; }

#loading {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1;
  background-color: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(2px);
  opacity: 0;
  display: none;
  animation: opacityFadeOut 0.3s ease-in-out forwards; }
  #loading.show {
    display: block;
    animation: opacityFadeIn 0.3s ease-in-out forwards; }

@keyframes opacityFadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes opacityFadeOut {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

body {
  color: black;
  display: flex;
  margin: 0;
  padding: 0;
  flex-direction: column; }

.welcome-page {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  text-align: center; }
  .welcome-page a {
    color: #333;
    font-weight: bold; }
